  background-color: #969591;
  z-index: 1000;
}

.language-form {
  display: flex;
  align-items: center;
  margin-bottom: 0;
}

/* Header */
ul.navbar-nav li.nav-item a.dropdown-toggle:after {
  display: none;
}

ul.navbar-nav li.nav-item a.nav-link:hover {
  color: #676663;
}

ul.navbar-nav li.nav-item a.nav-link:hover::before {
  background: #676663;
  width: 100%;
}

ul.navbar-nav li.nav-item a.nav-link::before {
  content: "";
  position: absolute;
  top: auto;
  bottom: -10px;
  height: 2px;
  width: 0;
  background: #676663;
  left: 0;
  right: auto;
  transition: all 0.3s ease 0s;
  margin: 0 auto;
}

ul.navbar-nav .dropdown .dropdown-menu {
  position: absolute;
  left: auto !important;
}

ul.navbar-nav .dropdown .dropdown-menu li a {
  background-color: #a8a29e;
}

ul.navbar-nav .dropdown:hover .dropdown-menu {
  opacity: 1 !important;
  visibility: visible !important;
  margin-top: 9px !important;
}

.header-fixed {
  width: 100%;
  padding: 0 !important;
  position: fixed !important;
  top: 0 !important;
  background-color: rgba(168, 162, 158, 0.9) !important;
  animation: slideDown 0.35s ease-in-out;
  z-index: 100;
}

.header-fixed nav {
  padding: 9px 0;
}

.lh-location .custom-select .custom-select::after {
